import React from 'react';
import { graphql, HeadProps, PageProps } from 'gatsby';
import HomePage from 'containers/HomePage';
import SEO from 'components/SEO';
import { getPageNumberFromPath, getPageSEOConfig } from './utils';

const PostsTemplate = (props: PageProps<Queries.PostsPageQuery>) => {
  return <HomePage {...props} />;
};

export default PostsTemplate;

export const Head = ({ pageContext, location }: HeadProps<Queries.PostsPageQuery, { lang: string }>) => {
  const pageNumber = getPageNumberFromPath(location.pathname);
  const pageSeoConfig = getPageSEOConfig(pageNumber);

  const alternateLanguages =
    pageContext.lang === 'ko'
      ? [{ lang: 'en', path: pageSeoConfig.enPath }]
      : [{ lang: 'ko', path: pageSeoConfig.koPath }];

  return (
    <SEO
      title={pageSeoConfig.title}
      description={
        pageContext.lang === 'ko' ? pageSeoConfig.descriptionByLanguage.ko : pageSeoConfig.descriptionByLanguage.en
      }
      lang={pageContext.lang}
      pathname={location.pathname}
      alternateLanguages={alternateLanguages}
      includeXDefaultAlternate={true}
    />
  );
};

export const pageQuery = graphql`
  query PostsPage($skip: Int!, $limit: Int!, $lang: String!) {
    allMarkdownRemark(
      skip: $skip
      limit: $limit
      sort: { frontmatter: { date: DESC } }
      filter: { fields: { lang: { eq: $lang } } }
    ) {
      edges {
        node {
          ...Post
        }
      }
      pageInfo {
        currentPage
        pageCount
      }
    }
  }
`;
