import React from 'react';
import { styled, useTheme } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Pagination from '@mui/material/Pagination';
import PaginationItem from '@mui/material/PaginationItem';
import { Link } from 'gatsby';
import { useCallback, useMemo } from 'react';
import { useSiteMetadata } from 'hooks/useSiteMetadata';

const Root = styled(Box)(({ theme }) => ({
  marginTop: theme.spacing(4),
}));

interface Props {
  pageCount: number;
  currentPage: number;
  defaultUrl?: string;
  lang: string;
}
const Paginator = ({ pageCount, currentPage, defaultUrl = '/', lang }: Props) => {
  const { defaultLanguage } = useSiteMetadata();
  const pageLanguage = useMemo(() => (lang === defaultLanguage ? '' : lang), [defaultLanguage, lang]);
  const { palette } = useTheme();

  const getLink = useCallback(
    (page: number) => {
      return page === 1 ? `${defaultUrl}${pageLanguage}` : `${defaultUrl}page/${page}/${pageLanguage}`;
    },
    [defaultUrl, pageLanguage]
  );

  return (
    <Root display="flex" justifyContent="center">
      <Pagination
        count={pageCount}
        page={currentPage}
        size="medium"
        shape="rounded"
        renderItem={item => <PaginationItem component={Link} to={getLink(item.page ?? -1)} {...item} />}
        sx={{
          '& .Mui-disabled': {
            visibility: 'hidden',
          },
          '& .Mui-selected': {
            backgroundColor: palette.grey[200],
          },
        }}
      />
    </Root>
  );
};

export default Paginator;
