export const getPageSEOConfig = (pageNumber: number) => {
  const isFirstPage = pageNumber === 1;

  if (isFirstPage) {
    return {
      koPath: '/',
      enPath: '/en/',
      title: undefined,
      descriptionByLanguage: {
        ko: '개발자의 집필 공간. 표상에서 그치는 것이 아닌, 본질을 탐구하고 사유하는 삶.',
        en: 'A collection of posts by Evan Moon on software, engineering culture, and thoughtful problem solving.',
      },
    };
  }

  return {
    koPath: `/page/${pageNumber}/`,
    enPath: `/page/${pageNumber}/en/`,
    title: `Evans Library | Page ${pageNumber}`,
    descriptionByLanguage: {
      ko: `개발자의 집필 공간. 표상에서 그치는 것이 아닌, 본질을 탐구하고 사유하는 삶의 ${pageNumber}번째 페이지.`,
      en: `Page ${pageNumber} of Evans Library posts. Explore more writing on software and engineering thinking.`,
    },
  };
};

export const getPageNumberFromPath = (pathname: string) => {
  const match = pathname.match(/^\/page\/(\d+)\/(?:en\/)?$/);

  if (match == null) {
    return 1;
  }

  const pageNumber = Number(match[1]);

  return Number.isNaN(pageNumber) ? 1 : pageNumber;
};